import React,{useState, useEffect} from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Home from './home';
import reportWebVitals from './reportWebVitals';
import Layout from './layout';
import Chart from './chart';
import Contact from './contact';
import Classify from './classify';
import dayjs from 'dayjs';
import Context from './store'

const App = () => {
  const [date,setDate] = useState([])
  const [showHome,setShowHome] = useState(true)
  const [showChart,setShowChart] = useState(false)
  const [showContact,setShowContact] = useState(false)
  const [showClassify,setShowClassify] = useState(false)
  useEffect(()=>{
    // 获取今天的0点
    const todayStart = dayjs().startOf('day').format('YYYY-MM-DD HH:mm:ss');    
    // 获取今天的23点59分59秒
    const todayEnd = dayjs().endOf('day').format('YYYY-MM-DD HH:mm:ss');
    setDate([todayStart,todayEnd])
  },[])

  return (
      <Context.Provider value={{date,setDate,setShowHome,setShowChart,setShowContact,setShowClassify}}>
        <Layout>
          {showHome && <Home />}
          {showChart && <Chart />}
          {showContact && <Contact />}
          {showClassify && <Classify />}
        </Layout>
      </Context.Provider>
  );
};
ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);


reportWebVitals();
